$('#formPost').validate({
    onfocusout: false,
    onkeyup: false,
    onclick: false,
    rules: {
        "district": {
            required: true,
            maxlength: 15
        },
        "password": {
            required: true,
            minlength: 8
        },
        "re-password": {
            equalTo: "#password",
            minlength: 8
            
        }
    },
    messages: {
        "user": {
            required: "Bt buc nhp username",
            maxlength: "Hy nhp ti a 15 k t"
        },
        "password": {
            required: "Bt buc nhp password",
            minlength: "Hy nhp t nht 8 k t"
        },
        "re-password": {
            equalTo: "Hai password phi ging nhau",
            minlength: "Hy nhp t nht 8 k t"
        }
    }
})